<template>
  <BModal
    id="modal-notify"
    dialog-class="notify-modal"
    :title="$t('Notify Me')"
    :visible="show"
    @hide="$emit('close')"
    centered
    hide-footer
    cancel-disabled
  >
    <p>Ontvang een melding zodra het product weer beschikbaar is.</p>
    <div class="notify-input-wrap">
      <div class="notify-input">
        <BFormInput
          id="notify-email"
          v-model.trim="email"
          :state="emailState"
          aria-describedby="invalid-notify-email"
          :placeholder="$t('Enter email to get notified')"
          v-if="!isLoggedIn"
        ></BFormInput>

        <BFormInvalidFeedback id="invalid-notify-email">
          {{ emailError }}
        </BFormInvalidFeedback>
      </div>
      <button class="c-btn c-btn-primary btn-notify" @click="submit">
        {{ $t("notify_btn") }}
      </button>
    </div>
  </BModal>
</template>

<script setup>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";

// Props
const props = defineProps({
  show: Boolean,
  productSku: String,
});

// Emits
const emit = defineEmits(["close"]);

// Store & i18n
const store = useStore();
const { t } = useI18n();

// Reactive Data
const emailReg =
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
const email = ref("");
const emailState = ref(null);
const emailError = ref("");

// Computed Properties
const isLoggedIn = computed(() => store.getters["user/getIsLoggedIn"]);

// Methods
async function submit() {
  if (email.value.length === 0 && !isLoggedIn.value) {
    emailState.value = false;
    emailError.value = t("email_is_required_field");
  } else if (!emailReg.test(email.value) && !isLoggedIn.value) {
    emailState.value = false;
    emailError.value = t("email_is_invalid_field");
  } else {
    emailState.value = true;
    emailError.value = "";
  }

  if (emailState.value) {
    const retval = await store.dispatch("product/addNotifyEmail", {
      email: email.value,
      sku: props.productSku,
    });

    if (retval === true) {
      const msg = {
        type: "success",
        title: t("Notify Me"),
        text: t("notify_subscribe_success"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
      email.value = "";
      emit("close");
    }
  }
}
</script>
