<template>
  <div>
    <div v-if="editAddressId == 0">
      <div class="account-address-book">
        <h1>{{ $t("address_book") }}</h1>
        <div class="address-block">
          <h2>{{ $t("default_addresses") }}</h2>
          <BRow>
            <BCol sm="6">
              <h3>{{ $t("default_billing_address") }}</h3>
              <div v-if="defaultBilling == null">
                {{ $t("no_default_billing_address") }}
              </div>
              <address v-if="defaultBilling != null">
                <span
                  >{{ defaultBilling.firstname }}
                  {{ defaultBilling.lastname }}</span
                >
                <span v-if="defaultBilling.company != null">{{
                  defaultBilling.company
                }}</span>
                <span>{{ defaultBilling.street[0] }}</span>
                <span
                  >{{ defaultBilling.city }},
                  {{ defaultBilling.postcode }}</span
                >
                <span>{{ defaultBilling.country_code }}</span>
                <span
                  >T:<a :href="`tel:` + defaultBilling.telephone">
                    {{ defaultBilling.telephone }}</a
                  ></span
                >
                <span v-if="defaultBilling.vat_id != null"
                  >VAT:{{ defaultBilling.vat_id }}</span
                >
              </address>
              <BLink
                class="link"
                @click="goToEditAddress(defaultBilling?.id)"
                >{{ $t("edit_address") }}</BLink
              >
            </BCol>
            <BCol sm="6">
              <h3>{{ $t("default_shipping_address") }}</h3>
              <div v-if="defaultShipping == null">
                {{ $t("no_default_shipping_address") }}
              </div>
              <address v-if="defaultShipping != null">
                <span
                  >{{ defaultShipping.firstname }}
                  {{ defaultShipping.lastname }}</span
                >
                <span v-if="defaultShipping.company != null">{{
                  defaultShipping.company
                }}</span>
                <span>{{ defaultShipping.street[0] }}</span>
                <span
                  >{{ defaultShipping.city }},
                  {{ defaultShipping.postcode }}</span
                >
                <span>{{ defaultShipping.country_code }}</span>
                <span
                  >T:<a :href="`tel:` + defaultShipping.telephone">
                    {{ defaultShipping.telephone }}</a
                  ></span
                >
                <span v-if="defaultShipping.vat_id != null"
                  >VAT:{{ defaultShipping.vat_id }}</span
                >
              </address>
              <BLink
                class="link"
                @click="goToEditAddress(defaultShipping?.id)"
                >{{ $t("edit_address") }}</BLink
              >
            </BCol>
          </BRow>
        </div>
        <div class="address-block">
          <h2>Adressen toevoegen</h2>
          <div class="address-table-wrap">
            <h3 v-if="addressList && addressList.length === 0">
              U heeft geen andere adressen toegevoegd aan uw adressenboek.
            </h3>
            <BTable
              v-if="addressList && addressList.length !== 0"
              :key="addressList"
              class="address-table"
              responsive
              :items="addressList"
              :per-page="selectedOption"
              :fields="fields"
            >
              <template v-slot:cell(btn)="data">
                <span class="adjust"
                  ><BLink @click="Adjust(data.item.id)">{{
                    data.value.label
                  }}</BLink></span
                >
                <span class="remove"
                  ><BLink @click="Remove(data.item.id)">{{
                    data.value.label2
                  }}</BLink></span
                >
              </template>
            </BTable>
            <div
              v-if="addressList && addressList.length !== 0"
              class="select-group d-flex justify-content-end"
            >
              <span>Toon</span>
              <div class="select-wrap">
                <BFormSelect
                  v-model="selectedOption"
                  :options="options"
                  class="select"
                ></BFormSelect>
              </div>
              <span>per pagina</span>
            </div>
          </div>
        </div>
        <div class="form-footer">
          <BButton
            @click="newAddress()"
            type="button"
            variant="info"
            class="text-white form-submit-btn"
            >Adres toevoegen</BButton
          >
        </div>
      </div>
    </div>
    <AccountEditAddress v-if="editAddressId != 0" />
  </div>
</template>

<script setup>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";
import AccountEditAddress from "@/base/core/components/account/AccountEditAddress.vue";

const { t } = useI18n();
const store = useStore();

const selectedOption = ref(10);
const options = [10, 20, 30];

const fields = ref([
  {
    key: "firstname",
    label: t("form_first_name"),
  },
  {
    key: "lastname",
    label: t("form_last_name"),
  },
  {
    key: "street[0]",
    label: t("form_street"),
  },
  {
    key: "city",
    label: t("form_city"),
  },
  {
    key: "country_code",
    label: t("form_country"),
  },
  {
    key: "postcode",
    label: t("form_postal_code"),
  },
  {
    key: "telephone",
    label: t("form_phone"),
  },
  {
    key: "btn",
    label: "",
  },
]);

const tabIndex = computed({
  get() {
    return store.getters["user/getAccountTabIndex"];
  },
  set(val) {
    store.commit("user/setAccountTabIndex", val);
  },
});

const editAddressId = computed({
  get() {
    return store.getters["user/getEditAddressId"];
  },
  set(val) {
    store.commit("user/setEditAddressId", val);
  },
});

const user = computed(() => store.getters["user/getCurrentUser"]);
const defaultBilling = computed(
  () => store.getters["user/getDefaultBillingAddress"]
);
const defaultShipping = computed(
  () => store.getters["user/getDefaultShippingAddress"]
);

const addressList = computed(() => {
  if (typeof user.value.addresses != "undefined") {
    const addresses = user.value.addresses;
    const addr = [];
    addresses.forEach((element) => {
      const btn = {
        label: t("edit"),
        label2: t("delete"),
      };
      element.btn = btn;
      addr.push(element);
    });
    return addr;
  }
  return [];
});

const newAddress = () => {
  editAddressId.value = -1;
};

const Adjust = (id) => {
  editAddressId.value = id;
};

const Remove = (id) => {
  store.dispatch("user/deleteAddress", { id, store });
};

const goToEditAddress = (id = -1) => {
  tabIndex.value = 3;
  store.commit("user/setEditAddressId", id);
};

// const goToEditPassword = () => {
//   tabIndex.value = 4;
// };
</script>
