<template>
  <div class="account-informate">
    <h1>{{ $t("change_email_info") }}</h1>
    <BRow>
      <BCol md="6">
        <BFormGroup
          class="account-inputs"
          id="email-group"
          :label="$t('form_email') + '*'"
          label-for="email"
        >
          <BFormInput
            id="email"
            v-model="form.email"
            type="text"
            :state="form.emailState"
            @blur="validateEmail"
            aria-describedby="email-feedback"
          ></BFormInput>
          <BFormInvalidFeedback id="email-feedback">
            {{ form.emailError }}
          </BFormInvalidFeedback>
        </BFormGroup>
        <BFormGroup
          class="account-inputs"
          id="password-group"
          :label="$t('form_password') + '*'"
          label-for="password"
        >
          <BFormInput
            id="password"
            v-model="form.password"
            type="password"
            :state="form.passwordState"
            aria-describedby="password-feedback"
          ></BFormInput>
          <BFormInvalidFeedback id="password-feedback">
            {{ form.passwordError }}
          </BFormInvalidFeedback>
        </BFormGroup>
      </BCol>
    </BRow>
    <div class="form-footer">
      <BButton @click="submit" variant="info" class="text-white form-submit-btn">Opslaan</BButton>
    </div>
  </div>
</template>

<script setup>
import { reactive, computed, watchEffect } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";

const store = useStore();
const { t } = useI18n();

const emailReg =
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;

const form = reactive({
  email: "",
  emailState: null,
  emailError: "",
  password: "",
  passwordState: null,
  passwordError: "",
});

const user = computed(() => store.getters["user/getCurrentUser"]);

watchEffect(() => {
  form.email = user.value.email;
});

const validateEmail = () => {
  if (form.email.length === 0) {
    form.emailState = false;
    form.emailError = t("email_is_required_field");
    return false;
  }
  if (!emailReg.test(form.email)) {
    form.emailState = false;
    form.emailError = t("email_is_invalid_field");
    return false;
  }
  form.emailState = true;
  return true;
};

const validatePassword = () => {
  const trimmedPassword = form.password.trim();
  if (trimmedPassword == "") {
    form.passwordState = false;
    form.passwordError = t("password_is_required_field");
  } else {
    form.passwordState = true;
  }
};

const submit = async () => {
  let formValid = true;

  if (form.emailState != true) {
    validateEmail();
    if (form.emailState != true) {
      formValid = false;
    }
  }

  if (form.passwordState != true) {
    validatePassword();
    if (form.passwordState != true) {
      formValid = false;
    }
  }

  if (formValid) {
    const retval = await store.dispatch("user/updateAccount", {
      account: form,
      store,
    });
    if (retval == true) {
      const msg = {
        type: "success",
        title: t("account_modify"),
        text: t("account_modified"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
    }
  }
};
</script>
